/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

body {
	&.cursor__grab {
		cursor: grab;
	}
}

#blockordion-drag-drop {
	position: absolute;
	background-color: white;
	border: 3px solid #ddd;
	opacity: 0.6;
}

.wp-block-imandresi-blockordion {
	$main_padding: 2px;

	padding-left: $main_padding;
	padding-right: $main_padding;
	padding-bottom: $main_padding;

	.components-dropdown {
		font-family: sans-serif;
	}
}

.blockordion__item {
	position: relative;
	$active_color: #e5f3ff;

	&::before {
		display: block;
		content: "";
		width: 0;
		height: 0;
		margin-left: auto;
		margin-right: auto;
		background-color: #d06002;
		transition: width 0.5s;
	}

	&.blockordion__drag-over {
		&::before {
			width: 100%;
			height: 8px;
			margin-top: 4px;
			margin-bottom: 4px;
		}
	}

	.blockordion__mouseover_area {
		position: absolute;
		left: 0;
		top: 0;
		width: 0;
		height: 0;
		background-color: transparent;
		//background-color: yellow;
		z-index: 20;

		&.blockordion__mouseover_area--active {
			width: 100%;
			height: 100%;
		}

	}

	header {
		justify-content: flex-start;
		align-items: center;
		padding-left: 5px;

		.blockordion__drag-handle {
