		.blockordion__navbar {
			flex-grow: 1;

			.blockordion__button {
				width: 36px;
				height: 36px;
				background-size: 20px;
			}
		}
	}

	&.blockordion__active {
		background-color: $active_color;

		header {
			background-color: $active_color;
		}

	}

}



#test-container {
	display: flex;
	flex-direction: row;

	#test-box-1 {
		width: 100px;
		height: 100px;
		background-color: yellow;
		margin: 40px;

	}

	#test-box-2 {
		width: 300px;
		height: 300px;
		background-color: red;
		margin: 40px;

	}
}
